/**
Needs to be set otherwise no shadow shows on close of the navigation menu.
 */
button:focus:not(:focus-visible) {
  box-shadow: 2px 2px 5px #f4f0ec !important;
}
/**
header-text needs to be included inside the parent element on hover
so the color changes together with the shadow on hover even if
the mouse does not hover on the text itself.
:focus and :active box-shadow should be !important otherwise
the default light blue outline shows.
 */
.btn-header {
  background-color: rgba(0,0,0,0.5);
  box-shadow: 2px 2px 5px #f4f0ec !important;
  &:hover {
    box-shadow: 2px 2px 5px #ff7f50 !important;
    .header-text {
      color: #ff7f50;
    }
  }
  &:focus,
  &:active {
    box-shadow: 2px 2px 5px #f4f0ec !important;
  }
}
.header-text {
  color: white;
}
.header-container {
  height: 70px;
  //background-color: #ffeaa7;
  //background-image: linear-gradient(315deg, #ffeaa7 0%, #000000 74%);
  background-color: #ca7968;
  background-image: linear-gradient(315deg, #ca7968 0%, #0c0c0c 74%);
  padding-top: 10px;
  padding-bottom: 10px;
}
