@import url(https://fonts.googleapis.com/css2?family=Tangerine&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button:focus:not(:focus-visible){box-shadow:2px 2px 5px #f4f0ec !important}.btn-header{background-color:rgba(0,0,0,.5);box-shadow:2px 2px 5px #f4f0ec !important}.btn-header:hover{box-shadow:2px 2px 5px coral !important}.btn-header:hover .header-text{color:coral}.btn-header:focus,.btn-header:active{box-shadow:2px 2px 5px #f4f0ec !important}.header-text{color:#fff}.header-container{height:70px;background-color:#ca7968;background-image:linear-gradient(315deg, #ca7968 0%, #0c0c0c 74%);padding-top:10px;padding-bottom:10px}
.offcanvas-header{background-color:#ca7968;background-image:linear-gradient(315deg, #ca7968 0%, #0c0c0c 74%);color:#dcdcdc}.link-menu{color:#20b2aa}.link-menu:hover{color:#40e0d0}
.card{border-style:none !important}.card-header{background-color:#708090 !important;background-image:none !important}.calendar-date{color:#fff}.bible-passage{color:#fff}.accordion-button{background-color:#fff !important;color:#000 !important}.accordion-button:not(.collapsed){color:#c19a6b !important}.accordion-button:hover,.accordion-button:active{box-shadow:0 0 3px 1px #c19a6b !important;color:#c19a6b !important}.accordion-button:focus{color:#fff !important;background-color:#c19a6b !important;box-shadow:none !important}.accordion-body{font-style:oblique !important}.btn-golden{background-color:transparent !important;box-shadow:2px 2px 5px silver}.btn-golden:focus{box-shadow:2px 2px 5px #c19a6b !important}.btn-golden:focus .challenge-text{color:#c19a6b}.btn-golden:active{box-shadow:2px 2px 5px #c19a6b !important}.btn-golden:active .challenge-text{color:#c19a6b}.btn-golden:hover{box-shadow:2px 2px 5px #c19a6b !important}.btn-golden:hover .challenge-text{color:#c19a6b}.challenge-text{color:#c19a6b}.border-heart{color:#c19a6b}.heart{color:#b8860b;font-size:24px}.memory-verse-label{font-style:oblique}.CardHeaderSmall{display:none}.CardHeaderBig{display:none}@media screen and (max-width: 767px){.CardHeaderSmall{display:block}.card-text,.accordion-question,.accordion-body{font-size:.875rem}}@media screen and (min-width: 768px){.CardHeaderBig{display:block}}
.calendar-header-container {
    background-color: rgba(255,255,255,0.5);
}
.calendar-header-title {
    color: #b8860b;
}
.calendar-header-unicode {
    color: white;
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 1px solid #b8860b;
    border-radius: 50px;
    background-color: #b8860b;
}
.CalendarHeader {
    display: none;
}
@media screen and (max-width: 370px) {
    .CalendarHeader {
        display: block;
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
        font-size: 20px;
    }
    .calendar-header-unicode {
        width: 30px;
        height: 30px;
    }
}
@media screen and (min-width:371px) and (max-width: 767px) {
    .CalendarHeader {
        display: block;
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .calendar-header-unicode {
        width: 35px;
        height: 35px;
    }
}
@media screen and (min-width: 768px) {
    .CalendarHeader {
        display: block;
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
    }
}

.btn-ornament{background-color:rgba(255,255,255,.5) !important;color:#c19a6b !important}.btn-ornament:hover{background-color:#c19a6b !important;color:#fff !important}.btn-ornament-past{background-color:#c5b358 !important;color:#fff !important}.btn-ornament-past:hover{background-color:#b5a642 !important;color:#fff !important}@media screen and (max-width: 375px){.btn-ornament-text{font-size:.75rem !important}}@media screen and (min-width: 376px)and (max-width: 767px){.btn-ornament-text{font-size:.875rem !important}}
.CalendarBig{
    display: none;
}
.CalendarSmall{
    display: none;
}
.CalendarXS{
    display: none;
}
@media screen and (max-width: 370px) {
    .CalendarXS {
        display: block;
    }
}
@media screen and (min-width:371px) and (max-width: 768px) {
    .CalendarSmall {
