.calendar-header-container {
    background-color: rgba(255,255,255,0.5);
}
.calendar-header-title {
    color: #b8860b;
}
.calendar-header-unicode {
    color: white;
    display: inline-block;
    width: 50px;
    height: 50px;
    border: 1px solid #b8860b;
    border-radius: 50px;
    background-color: #b8860b;
}
.CalendarHeader {
    display: none;
}
@media screen and (max-width: 370px) {
    .CalendarHeader {
        display: block;
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
        font-size: 20px;
    }
    .calendar-header-unicode {
        width: 30px;
        height: 30px;
    }
}
@media screen and (min-width:371px) and (max-width: 767px) {
    .CalendarHeader {
        display: block;
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
    }
    .calendar-header-unicode {
        width: 35px;
        height: 35px;
    }
}
@media screen and (min-width: 768px) {
    .CalendarHeader {
        display: block;
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
    }
}
