.CalendarBig{
    display: none;
}
.CalendarSmall{
    display: none;
}
.CalendarXS{
    display: none;
}
@media screen and (max-width: 370px) {
    .CalendarXS {
        display: block;
    }
}
@media screen and (min-width:371px) and (max-width: 768px) {
    .CalendarSmall {
        display: block;
        font-size: 0.875rem;
    }
}
@media screen and (min-width: 769px) {
    .CalendarBig {
        display: block;
    }
}
