.card {
    border-style: none !important;
}
.card-header {
    background-color: rgb(112,128,144) !important;
    background-image: none !important;
}
.calendar-date {
    color: white;
}
.bible-passage {
    color: white;
}
.accordion-button {
    background-color: white !important;
    color: black !important;
}
.accordion-button:not(.collapsed) {
    color: #c19a6b !important;
}
.accordion-button:hover,
.accordion-button:active {
    box-shadow: 0 0 3px 1px #c19a6b  !important;
    color: #c19a6b !important;
}
.accordion-button:focus {
    color: white !important;
    background-color: #c19a6b !important;
    box-shadow: none !important;
}
.accordion-body {
    font-style: oblique !important;
}
.btn-golden {
    background-color: transparent !important;
    box-shadow: 2px 2px 5px #c0c0c0;
    &:focus {
        box-shadow: 2px 2px 5px #c19a6b !important;
        .challenge-text {
            color: #c19a6b;
        }
    }
    &:active {
        box-shadow: 2px 2px 5px #c19a6b !important;
        .challenge-text {
            color: #c19a6b;
        }
    }
    &:hover {
        box-shadow: 2px 2px 5px #c19a6b !important;
        .challenge-text {
            color: #c19a6b;
        }
    }
}
.challenge-text {
    color: #c19a6b;
}
.border-heart {
    color: #c19a6b;
}
.heart {
    color: #b8860b;
    font-size: 24px;
}
.memory-verse-label {
    font-style: oblique;
}
.CardHeaderSmall {
    display: none;
}
.CardHeaderBig {
    display: none;
}

@media screen and (max-width: 767px) {
    .CardHeaderSmall {
        display: block;
    }
    .card-text,
    .accordion-question,
    .accordion-body {
        font-size: 0.875rem;
    }
}
@media screen and (min-width: 768px) {
    .CardHeaderBig {
        display: block;
    }
}
